import Image from "next/image";
import Link from "next/link";
import React, { useEffect, useState } from 'react';
import UserCard from '../components/UserCard';

const Home = () => {
  const [users, setUsers] = useState([]);
  const [search, setSearch] = useState('');
  const [timeoutId, setTimeoutId] = useState(null);

  useEffect(() => {
    const fetchUsers = async () => {
      const response = await fetch('https://dummyjson.com/users');
      const data = await response.json();
      setUsers(data.users);
    };

    fetchUsers();
  }, []);

  const handleSearch = (e) => {
    const value = e.target.value;
    setSearch(value);

    if (timeoutId) {
      clearTimeout(timeoutId);
    }

    const id = setTimeout(() => {
      // Фильтрация пользователей по имени
      const filteredUsers = users.filter(user => user.name.toLowerCase().includes(value.toLowerCase()));
      setUsers(filteredUsers);
    }, 300);

    setTimeoutId(id);
  };

  return (
    <div>
      <input type="text" placeholder="Поиск пользователей" value={search} onChange={handleSearch} />
      <div className="user-list">
        {users.map(user => (
          <User Card key={user.id} user={user} />
        ))}
      </div>
    </div>
  );
};

export default Home;